.Container{
    display: flex;
    align-items: center;
    gap:1%;
    position: sticky;
    top: 0;
    height: 54px;
    z-index: 100;
    margin-bottom: 5px;
    padding: 0 7px;
    transition: all 0.5s ease;
    backdrop-filter: blur(10px);
    justify-content: space-between;
}
.image{
    animation: rotation 3s infinite linear;
}
.leftcontainer{
  display: flex;
  cursor: pointer;
}
.rightcontainer{
  width: 25px;
  cursor: pointer;
}
@keyframes rotation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(359deg);
    }
  }